.navbar{

    position: fixed;
    top: 0;
    left: -300px;
    bottom: 0;
    width: 300px;
    transition: all ease-in-out 0.5s;
    z-index: 9997;
    transition: all 0.5s;
    padding: 0 15px;
    background: #040b14;
    overflow-y: auto;
}

.mostrar{

    left: 0;
}

.my-img-fluid{

    width: 60%;
    margin: 0 auto;
}

.banner-places{

    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    width: 100%;
    height: 100%;
    padding-top: 3rem;
}

.my-ubication{

    width: 100%;
}



.item-lista{

    
    padding: .5rem 1rem;
    margin-top: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.item-lista > div{

    
    padding: 8px 13px;
    text-decoration: none;
    color: white;
    font-size: 14px;
    font-weight: bolder;
    transition: all .8s ease-in-out;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 15px;
}

.item-lista > div:hover{

    color: black;
    background: white;
}